.friendList{
    list-style: none;
    margin: 0;
    padding: 0;
}

.item{
    margin: 0;
    padding: 0;
    width: 350px;
    height: 100px;
    box-shadow: 0px 1px 3px 0px rgba(191, 205, 222, 0.9);
    display: flex;
    justify-content: space-between;
}

.item:not(:last-child){
    margin-bottom: 15px;
}

.avatar{
    width: 80px;
    height: 80px;
    display: block;
    padding: 10px;
    border-radius: 18px;
    position: relative;
    right: -10%;
}

.name{
    padding-right: 40%;
    padding-top: 18px;
    font-size: 20px;
    color: #1f3349;
}

.status {
    display: block;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    position: absolute;
    margin-top: 35px;
    margin-left: 10px;
}

.active {
    composes: status;
    background-color: red;
}

.inactive {
    composes: status;
    background-color: green;
}