.statistics{
    margin: 0;
    padding: 0;
    width: 375px;
    height: 252px;
    background-color: #f0f3f6;
    padding: 40px 65px;
    margin: 0 auto;
}

.title{
    margin: 0;
    padding: 0;
        border-radius: 2px;
        background-color: rgb(255, 255, 255);
        box-shadow: 0px 2px 2px 0px rgba(2, 2, 2, 0.02);
        /* position: absolute;
        left: 54px;
        top: 61px; */
        width: 350px;
        height: 160px;
        margin: 0 auto;
         color: #475965;
         font-size: 20px;
         text-align: center;
         padding-top: 30px;
}

.statList{
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    align-content: center;
    align-items: center;
    bottom: 30%;
    width: 350px;
    position: absolute;
    top: -4%;
    left: 36.3%;
}

.label{
    display: block;
    font-size: 12px;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 10px;
}

.item{
    padding: 10px;
    width: 70px;
    height: 60px;
    color: #fff;
}

.percentage{
    display: block;
    margin: 0 auto;
    font-size: 18px;
    text-align: center;
}